html {
  box-sizing: border-box;
  font-size: 10px;
}

*, :before, :after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p, ul, li {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
