  font-size: 1.6rem;
  line-height: 2.6rem;
}

ul {
  list-style: none;
}

a {
  color: color-primary;
  text-decoration: none;
}

li {
  list-style-type: none;
}

img {
  display: block;
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

@media screen and (min-width: 1200px) {
  .section {
    padding-top: 94px;
    padding-bottom: 94px;
  }
}

.wrapper {
  max-width: 1197px;
  margin: auto;
}

h1 {
  font-size: 4.4rem;
  font-style: normal;
  font-weight: bolder;
  line-height: 5.4rem;
}

h2 {
  font-size: 3.4rem;
