}

.header {
  background-color: #fff;
  padding: 15px 0 17px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, .05);
}

.header__wrapper {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.navigation {
  font-size: 1.5rem;
  line-height: 2.8rem;
  display: flex;
}

.logo {
  align-items: center;
  display: flex;
}

.logo-text {
  width: 147px;
  height: 29px;
}

.logo-object {
  width: 30px;
  height: 30px;
}

.navigation__link {
  cursor: pointer;
  padding: 0 10px;
  font-weight: 400;
  transition: color .25s linear;
}

.navigation__link:hover, .navigation__link:focus, .navigation__link:active {
  color: #f89b05;
}

.navigation__item:not(:last-of-type) .navigation__link {
