  color: #544837;
  background-color: #fff;
  border: 1px solid #d7d7d7;
  transition: color .25s linear, background-color .25s linear;
}

.button_bordered:hover, .button_bordered:focus {
  color: #fff;
  background-color: #f90;
  border: 1px solid #f90;
}

.button_colored {
  color: #fff;
  background-color: #f90;
